body {
	margin: 0;
	background-color: $bg;
	color: var(--text);
	font-family: 'Ubuntu', sans-serif;
}

h1, p {
	&:first-child {
		margin-top: 0;
	}
}

h1, p {
	&:last-child {
		margin-bottom: 0;
	}
}

h1 {
	font-size: 4rem;
}

